:root{
	--primary-color: #03A9F4;
}

*{
	margin: 0;
	padding: 0;
}
body{
	font-family: 'Poppins',Arial,sans-serif;
	font-size: 1rem;
}

#app{
	max-width: 300px;
	margin: 0 auto;
}

.navbar{
	margin-top: 30px;
	border-bottom: 2px solid var(--primary-color);
	text-align: center;
	padding: 3px 0;
}
.navbar a{
	text-decoration: none;
	color: var(--primary-color);
	font-size: 2em;
}


.clear{
	text-align: right;
	color: #F44336;
	margin-bottom: 15px;
	letter-spacing: 1px;
}


input {
	outline: none;
	margin: 15px 0 10px 0;
	border: 2px solid var(--primary-color);
	padding: 10px;
	width: calc(100% - 24px);
	border-radius: 3px;
}


.todo{
	padding: 10px;
	color: #545454;
	background-color: #fff;
	border-radius: 3px;
	margin: 10px 0;
	box-shadow: 0 3px 10px rgba(0, 0, 0, 0.1);
}

.msg{
	text-align: center;
	color: #636363;
}



.progress-bar{
	height: 5px;
	background-color: #DEF7FF;
	border-radius: 2px;
	overflow: hidden;
}
.progress-bar .progress{
	height: 100%;
	background-color: #03A9F4;
	transition: width 0.3s;
}
